import React, { useState, useContext } from 'react';
import AppContext from './AppContext';
import PokedexMenuSprite from './PokedexMenuSprite';

export type TypesColorsTailwind = {
  [key: string]: string,
};

export const TYPES_TO_COLORS_TAILWIND: TypesColorsTailwind = {
  Normal: 'bg-neutral',
  Fire: 'bg-red',
  Water: 'bg-blue',
  Electric: 'bg-yellow',
  Grass: 'bg-green',
  Ice: 'bg-cyan',
  Fighting: 'bg-orange',
  Poison: 'bg-fuchsia',
  Ground: 'bg-slate',
  Flying: 'bg-sky',
  Psychic: 'bg-purple',
  Bug: 'bg-lime',
  Rock: 'bg-stone',
  Ghost: 'bg-slate',
  Dragon: 'bg-indigo',
  Dark: 'bg-gray',
  Steel: 'bg-zinc',
  Fairy: 'bg-pink',
};

function PokedexSpriteGridCell({
  speciesId,
}: {
  speciesId: string,
}) {
  const [hovered, setHovered] = useState(false);
  const { dex, setClickedSpeciesId, spritePosition } = useContext(AppContext);

  const { types } = dex[speciesId];
  const [type1] = types;
  /* Just in time variable needs to be declared in tailwindcss */
  const bgColor = TYPES_TO_COLORS_TAILWIND[type1];

  return (
    <>
      {
        /* eslint-disable */
        // HACKY ESLINT-DISABLED FOR JSX
      }
      <div
        onMouseEnter={() => setHovered(true)}
        onMouseLeave={() => setHovered(false)}
        onClick={() => setClickedSpeciesId(speciesId)}
        className={`${bgColor}-${hovered ? '300' : '100'} rounded-lg p-1 cursor-pointer`}
      >
        <PokedexMenuSprite key={`${speciesId}-sprite-grid`} currentSpritePosition={spritePosition[speciesId]} />
      </div>
    </>
  );
}

export default PokedexSpriteGridCell;
